.write
{
    padding-top: 50px;
}

.writeimg
{
    margin-left: 150px;
    width: 70vw;
    height: 300px;
    border-radius: 10px;
    object-fit: cover;
}
.writeform
{
    position: relative;
}

.formgroup
{
    margin-left: 150px;
    display: flex;
    align-items: center;
}

.plusicon
{
    width: 25px;
    height: 25px;
    border-radius: 25%;
    bottom: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: #666;
    cursor: pointer;
}

.writeInput
{
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
}

.writeInput:focus
{
    outline: none;
}

.writeText
{
    font-size: 20px;
    height: 100vh;
}

.writeSubmit
{
    position: absolute;
    top: 20px;
    right: 50px;
    color: white;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
}
